import React, { useEffect, useRef } from "react"
import {
  StyledBanner,
  Canvas,
  BannerTitle,
  Headline,
  BackgroundImage,
  SmallHeadline,
} from "../../styles/homeStyles"
import useWindowSize from "./../../hooks/useWindowSize"
import { useGlobalStateContext } from "../../context/globalContext"
import backgroundImage from "../../assets/png/background1.png"
// console.log("off")
// console.log(canvas.offsetLeft, canvas.offsetTop)
// console.log("page")
// console.log(e.pageX, e.pageY)
// console.log("cur")
// console.log(curX, curY)
const Banner = () => {
  let canvasRef = useRef(null)
  const size = useWindowSize()
  const { currentTheme } = useGlobalStateContext()

  const handleCanvas = () => {
    let canvas = canvasRef.current
    let ctx = canvas.getContext("2d")
    ctx.fillStyle = "#ffffff"
    ctx.fillRect(0, 0, size.width, size.height)
    let onCanvas = false
    let savedX, savedY

    canvas.addEventListener("mouseenter", e => {
      onCanvas = true
      savedX = e.offsetX
      savedY = e.offsetY
    })
    canvas.addEventListener("mouseleave", e => {
      onCanvas = false
      savedX = e.offsetX
      savedY = e.offsetY
    })

    canvas.addEventListener("mousemove", e => {
      if (onCanvas) {
        ctx.globalCompositeOperation = "destination-out"
        let curX = e.offsetX
        let curY = e.offsetY
        console.log("cur")
        console.log(curX, curY)
        console.log("saved")
        console.log(savedX, savedY)
        ctx.lineJoin = "round"
        ctx.beginPath()
        ctx.moveTo(savedX, savedY)
        ctx.lineTo(curX, curY)
        ctx.closePath()
        ctx.lineWidth = 100
        ctx.stroke()
        savedX = curX
        savedY = curY
      }
    })
  }

  const parent = {
    initial: { y: 800 },
    animate: {
      y: 0,
      transition: {
        staggerChildren: 0.2,
      },
    },
  }
  const child = {
    initial: { y: 800 },
    animate: {
      y: 0,
      transition: {
        duration: 1,
        ease: [0.6, 0.05, -0.01, 0.9],
      },
    },
  }

  useEffect(() => {
    handleCanvas()
  }, [currentTheme])

  return (
    <StyledBanner>
      <BackgroundImage>
        <img src={backgroundImage} alt="AI app" />
      </BackgroundImage>
      <Canvas
        width={`${size.width}px`}
        height={`${size.height}px`}
        ref={canvasRef}
      />
      <BannerTitle variants={parent} initial="initial" animate="animate">
        <SmallHeadline variants={child}>
          What's on your client's mind?
        </SmallHeadline>
        <Headline variants={child}>START</Headline>
        <Headline variants={child}>DIGGIN'</Headline>
      </BannerTitle>
    </StyledBanner>
  )
}

export default Banner
